.section {
    border: 1px dashed gray;
    padding: 5px;
    margin-bottom: 25px;
    background-color: white;
    cursor: move;
    max-width: fit-content;
    width: 750px;
    /* width: 800px; */
    /* overflow: auto; */
}

/* .section < */

.add-section {
    border: 1px dashed gray;
    border-radius: 10px;
    padding: 25px;
    width: 550px;
    height: 60px;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.icon {
    margin: auto;
    width: 20px;
    height: 20px;
    text-align: center;
    color: gray;
}

.icon-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.add-icon {
    width: 40px;
    height: 40px;
    margin: auto 20px;
}

input {
    border: 0;
    outline: 0;
}

.story-container {
    width: 853px;
    margin: auto;
    padding: 50px 0;
    text-align: center;
}

.story-container > * {
    display: inline-block;
}

.headline {
    font-size: 2.5rem;
    line-height: 2.875rem;
    margin: 10px auto;
}

/* .headline h1 {
    font-size: 2.5rem;
    line-height: 2.875rem;
    margin: 10px auto;
} */

.byline {
    font-size: 1.25rem;
    line-height: 1.875rem;
    margin-bottom: 20px;
    color: grey;
}

.body {
    font-size: 1.25rem;
    line-height: 1.875rem;
}

.text {
    width: 600px;
    margin: auto;
    font-family: 'georgia';
    color: #363636;
    text-align: left;
}

