    resize: none;
    margin: auto;
    border: 0;
    outline: 0;
    text-align: left;
} */

.image-container {
    width: 600px;
    margin: auto;
    position: relative;
}

.image {
    /* max-width: 1000px; */
    display: block;
    width: 100%;
    height: auto;
}

.circle {
    border-radius: 50%;
    background-color: white;
    width: 50px;
    height: 50px;
    margin: auto;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    cursor: pointer;
}

.circle .icon {
    margin-top: 12px;
}

.upload {
    position: absolute;
    top: 0;
    display: block;
    background: rgba(0, 0, 0, 0.2);
    height: 100%;
}

.scrolly {
    /* padding-bottom: 100%;
    margin: 5% 0 -95%; */
}

.background-image-sticky {
    width: 100%;
    height: auto;
    margin: auto;
    position: sticky;
    top: 0;
    left: 50%;
}

.text-overlay {
    background-color: rgba(255, 255, 255, 0.7);
    position: relative;
    margin: auto;
    padding: 20px 25px;
    z-index: 1;
}

iframe {
    min-height: 600px;
}